.popup {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 20;
  background-color: rgba(10, 39, 79, 0.3);
  &__inner {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 1030px;
    width: 100%;
    padding: 90px 70px;
    background-color: #ffffff;
  }
  & .request {
    padding-bottom: 0;
  }
}